import React, { FC } from 'react';
import { ColumnsProps, DefaultSortProps, FilterType, getExtraData, OrderType, SearchDataGrid, SearchViewProps, WindowComponent } from 'summer';
import { CLASS_NAME, ConfigSimplesNacional } from '../../../../models/dtos/mlf/configSimplesNacional';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'descricao',
    order: OrderType.ASC,
  },
];

const ConfigSimplesNacionalSearchView: FC<SearchViewProps<ConfigSimplesNacional>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'descricao',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
    {
      dataField: 'diaVencimento',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Dia de Vencimento',
    },
  ];
  return (
    <WindowComponent title="Configurações do Simples Nacional">
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        defaultSorted={defaultSorted}
        dtoClassName={CLASS_NAME}
        extraData={getExtraData(props, 'ConfigSimplesNacionalEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default ConfigSimplesNacionalSearchView;
